<template>
  <div>
     <el-table :data="list"
     row-key="id"
      border style="width: 100%">
      <el-table-column prop="id" label="菜单编号" width="120"> </el-table-column>
      <el-table-column prop="title" label="菜单名称" width="140">
      </el-table-column>
      <el-table-column prop="icon" label="菜单图标" width="300"> </el-table-column>
      <el-table-column prop="url" label="菜单地址" width="300"> </el-table-column>
      <el-table-column prop="status" label="状态" width="100">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1">启用</el-button>
          <el-button type="danger" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="操作" width="*">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {requestMenuDel} from '../../../util/request';
import {mapGetters,mapActions} from 'vuex';
import { successAlert, warningAlert } from '../../../util/alert';
export default {
  computed:{
    ...mapGetters({
      list:"menu/list"
    })
  },
  methods:{
      ...mapActions({
        requestList:'menu/menuList',
      }),
      edit(id){
        this.$emit('edit',id);
      },
      del(id){
        this.$confirm('确定要删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(()=>{
          requestMenuDel({id:id}).then(res=>{
            if(res.data.code == 200){
              successAlert('删除成功！！！')
              this.requestList();
            }else{
              warningAlert('删除失败');
            }
          })
        }).catch(error=>error)
      }
  },
  mounted(){
    // 自动发送请求
    this.requestList();
  }
}
</script>

<style scoped>

</style>



