<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show">
  <el-form :model="form">
    <el-form-item label="菜单名称" :label-width="formLabelWidth">
      <el-input v-model="form.title" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="上级菜单" :label-width="formLabelWidth">
      <el-select v-model="form.pid">
        <el-option label="--请选择--" value="" readonly></el-option>
        <el-option label="顶级菜单" :value="0"></el-option>
        <!-- 动态请求数据 -->
        <el-option v-for="item in list" :key="item.id" :label="item.title" :value="item.id"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="菜单类型" :label-width="formLabelWidth">
      <!-- 单选变化的label则为动态变量 -->
       <el-radio v-model="form.type" :label="1">目录</el-radio>
       <el-radio v-model="form.type" :label="2">菜单</el-radio>
    </el-form-item>
    <el-form-item label="菜单图标" :label-width="formLabelWidth" v-if="form.type==1">
      <el-select v-model="form.icon" >
        <el-option label="--请选择--" value="" readonly></el-option>
        <el-option v-for="item in icons" :key="item" :value="item"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="菜单地址" :label-width="formLabelWidth" v-else>
      <el-select v-model="form.url">
        <el-option label="--请选择--" value="" readonly></el-option>
        <el-option v-for="item in urls" :key="item" :value="item"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="菜单状态" :label-width="formLabelWidth">
      <el-switch  v-model="form.status" active-color="blue" inactive-color="grey" :active-value="1"
    :inactive-value="2">
</el-switch>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="add" v-if="info.isAdd==true">添加</el-button>
    <el-button type="primary" @click="update" v-else>修改</el-button>
  </div>
</el-dialog>

  </div>
</template>

<script>
import {requestMenuAdd,requestMenuOne,requestMenuEdit} from '../../../util/request';
import {successAlert,warningAlert} from '../../../util/alert';
import {mapGetters, mapActions} from 'vuex';
export default {
  props:["info"],
  computed:{
    ...mapGetters({
      list:"menu/list",
    })
  },
  data() {
      return {
        form: {
          pid: 0,
          title:'',
          icon: '',
          type: 1,
          url: '',
          status:1,
        },
        icons:[
          "el-icon-setting",
          "el-icon-user",
          "el-icon-help",
          "el-icon-s-home",
          "el-icon-s-grid"
        ],
        urls:[
          'menu',
          'role',
          'manage',
          'goods',
          'cate',
          'spec',
          'goods',
        ],
        formLabelWidth: '120px',
        value:true
      };
    },
    methods:{
      // 重置内容
      empty(){
        this.form = {
          pid: 0,
          title:'',
          icon: '',
          type: 1,
          url: '',
          status:1,
        }
      },
      cancel(){
        this.info.show = false
        if(!this.info.isAdd){
          this.empty();
        }
      },
      add(){
        this.info.show = false;
        // 发送axios请求
        requestMenuAdd(this.form).then((res)=>{
          if(res.data.code === 200){

            successAlert(res.data.msg);
            //重置form数据
            this.empty();
            //弹框消失
            this.cancel();
             this.requestList();
          }else{
            warningAlert(res.data.msg);
          }
        })

      },
      getDetail(id){
        // 根据ID获取某一调数据
        requestMenuOne({id:id}).then(res=>{
          // 将获取的该条数据展示在对话框中
          this.form = res.data.list;
          this.form.id = id;
        })
      },
      update(){
        // 发送修改请求
        requestMenuEdit(this.form).then(res=>{
          if(res.data.code == 200){
            successAlert('修改成功');
             this.empty()
            this.cancel()
            this.requestList();
          }else{
            warningAlert('修改失败');
          }
        })
      },
      ...mapActions({
        requestList:"menu/menuList"
      })
    },


}
</script>

<style>

</style>



